




























import { Component, Vue, Prop } from 'vue-property-decorator'

@Component({
  name: 'ProjectCard',
})
export default class ProjectCard extends Vue {
  @Prop(Boolean) readonly detail: boolean = false
  @Prop({ default: 'Academic Library' }) readonly title: string | undefined
  @Prop({ default: 'Academic libary showcasing univ books catalog' })
  readonly description: string | undefined
  @Prop({ default: () => [], type: Array }) readonly stacks:
    | Array<string>
    | undefined
  @Prop(String) readonly url: string | undefined
  @Prop(String) readonly imageUrl: string | undefined

  get detailClass(): string {
    return this.detail ? 'detail' : ''
  }
  get darkClass(): string {
    return this.$vuetify.theme.dark ? 'dark' : ''
  }
  get linkClass(): string {
    return this.url ? '' : 'disabled'
  }

  get firstWordTitle(): string {
    if (!this.title) return ''
    return this.title.split(' ')[0]
  }
  get restWordTitle(): string {
    const arrayTitles: Array<string> = this.title?.split(' ') || ['']
    arrayTitles.shift()
    return arrayTitles.join(' ')
  }

  get urlFile(): string {
    return this.imageUrl
      ? require(`@/assets/image/${this.imageUrl}`)
      : require('@/assets/image/project/blank.jpg')
  }

  clickProject(e: Event): void {
    if (!this.url) e.preventDefault()
  }
}
