









































import { Component, Vue } from 'vue-property-decorator'
import ProjectCard from '@/components/Base/ProjectCard.vue'
import Projects from '@/utils/projects'
import { Work } from '@/utils/work'

@Component({
  name: 'ProjectPage',
  components: {
    ProjectCard,
  },
})
export default class ProjectPage extends Vue {
  detail: number | string = ''
  projects: Array<Work> = Projects
  show = false
  clickCard(item: number): void {
    if (this.detail === item) this.detail = ''
    else this.detail = item
  }
  get scrollTop(): Array<string> {
    return this.show ? ['scroll-top', 'show'] : ['scroll-top']
  }
  onScroll(): void {
    const el = document.getElementById('project')
    if (el) {
      const getReact = el.getBoundingClientRect()
      if (getReact.bottom < getReact.height) {
        this.show = true
      } else {
        this.show = false
      }
    }
  }
}
