


















import { Component, Vue, Prop } from 'vue-property-decorator'

@Component({
  name: 'SkillCard',
})
export default class SkillCard extends Vue {
  @Prop({ default: 'Julia Effect' }) readonly caption!: string
  @Prop({ default: 'fe.jpg' }) readonly fileName!: string
  @Prop(Array) readonly items: Array<string> | undefined
  @Prop({ default: true }) readonly animated!: boolean

  get captions(): Array<string> {
    return this.caption.split(' ')
  }

  get animatedEffect(): string {
    return this.animated ? 'animated' : ''
  }
}
