

























































import { Component, Vue } from 'vue-property-decorator'
import TechCard from '@/components/Base/TechCard.vue'
import SkillCard from '../Base/SkillCard.vue'

interface skillCard {
  caption: string
  items: Array<string>
  fileName: string
}

@Component({
  name: 'Technical',
  components: {
    TechCard,
    SkillCard,
  },
})
export default class Technical extends Vue {
  get techClass(): Array<string> {
    return !this.$vuetify.theme.dark
      ? ['red', 'lighten-1']
      : ['grey', 'darken-3']
  }

  get frontEnd(): skillCard {
    return {
      caption: 'Front-end Development',
      items: ['html', 'css', 'scss', 'javascript', 'typescript', 'vue', 'nuxt'],
      fileName: 'fe.jpg',
    }
  }
  get backEnd(): skillCard {
    return {
      caption: 'Back-end Development',
      items: ['php', 'node js', 'express'],
      fileName: 'be.jpg',
    }
  }
  get mobile(): skillCard {
    return {
      caption: 'Mobile Development',
      items: ['react-native'],
      fileName: 'mobile.jpg',
    }
  }
  get learning(): skillCard {
    return {
      caption: 'Learning',
      items: [
        'react',
        'react-native',
        'docker',
        'css animation',
        'bundler',
        'unit-test',
      ],
      fileName: 'book.jpg',
    }
  }
}
