











import { Component, Vue } from 'vue-property-decorator'
import Basic from '@/components/About/Basic.vue'
import Technical from '@/components/About/Technical.vue'

interface Goto {
  duration: number
  offset: number
  easing: string
}

@Component({
  name: 'About',
  components: {
    Basic,
    Technical,
  },
})
export default class About extends Vue {
  show = false
  get options(): Goto {
    return {
      duration: 300,
      offset: 0,
      easing: 'easeInOutCubic',
    }
  }
  get scrollTop(): Array<string> {
    return this.show ? ['scroll-top', 'show'] : ['scroll-top']
  }
  get buttonColor(): string {
    return this.$vuetify.theme.dark ? 'red' : 'grey lighten-1'
  }
  scrollAbout(): void {
    // if (window.innerHeight <= window.pageYOffset) {
    //   this.show = true
    // } else {
    //   this.show = false
    // }
    const el = document.getElementById('about')
    if (el) {
      const getReact = el.getBoundingClientRect()
      if (getReact.bottom < getReact.height) {
        this.show = true
      } else {
        this.show = false
      }
    }
  }
}
